/* eslint-disable */

import { useEffect, useState } from 'react';
import {useHistory, useParams} from 'react-router-dom'
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
    padding : 20px;
`;
let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.colors}
`;

function Detail(props){
    
    let [alert,alertf] = useState(true);

    useEffect(()=>{
        let timer = setTimeout(()=>{alertf(false)}, 2000);
        return ()=>{ clearTimeout(timer)}
    },[alert]);
    
    

    let history = useHistory();//사용자의 방문기록이 담겨있는 오브젝트
    let {id} = useParams();///:id에 있는 그 id임
    let found = props.shoe.find(function(product){
        return product.id == id
    });

    return(
        <div className="container">
            <박스>
                <제목 colors="red">Detail</제목>
                <제목 colors={'blue'}>Detail</제목>
            </박스>

            {
                alert === true
                ? (<div className="my-alert2">
                    <p>재고가 얼마 남지 않았습니다.</p>
                    </div>)
                : null
            }

            <div className="row">
                <div className="col-md-6">
                <img src={'https://codingapple1.github.io/shop/shoes' + (parseInt(id)+1) + '.jpg'} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                <h4 className="pt-5">{found.title}</h4>
                <p>{found.content}</p>
                <p>{found.price}원</p>
                <Info stock={props.stock}></Info>

                <button className="btn btn-danger" onClick={()=>{props.stockf([9,11,12])}}>주문하기</button> 
                <button className="btn btn-danger" onClick={()=>{
                    history.goBack();
                }}>뒤로가기</button> 
                </div>
            </div>
        </div>
    )
}

function Info(props){
    return (
        <p>재고 : {props.stock[0]}</p>
    )
}

export default Detail;